<template>
  <div class="schedule-sumary">
    <v-row>
      <v-col class="py-0">
        <ul v-show="selectedServices.length > 0" class="services">
          <li
            v-for="service in selectedServices"
            :key="service.id"
            class="service py-0"
          >
            {{ service.name }}
          </li>
        </ul>
        <span v-if="barberName" class="d-block barber cinza2--text">
          Com {{ barberName }}
        </span>
      </v-col>
      <v-col
        class="text-left text-sm-right d-flex flex-column justify-end py-0 mt-2 mt-sm-0"
        cols="12"
        sm="auto"
      >
        <span
          v-if="dateTime"
          class="gray3--text date"
        >
          {{ dateTime }}
        </span>
        <div class="primaryLight--text price">
          <small>Total</small> R$ {{ totalServicesValue | numeric }}
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import DateMixin from '@/mixins/DateMixin'
import TextMixin from '@/mixins/TextMixin'

export default {
  mixins: [ DateMixin, TextMixin ],
  props: {
    choosedDateTime: { default: null },
    barberName: { default: null },
    selectedServices: {
      default: () => []
    }
  },
  computed: {
    dateTime () {
      const choosedDateTime = (this.choosedDateTime) ? new Date(this.choosedDateTime) : null
      return choosedDateTime ? this.formatDateTimeToStringPtBr(choosedDateTime) : null
    },
    totalServicesValue () {
      return this.selectedServices.reduce((total, service) => {
        const value = Number(total) + Number(service.price)
        return Number(value).toFixed(2)
      }, 0)
    }
  }
}
</script>

<style scoped>
.services { list-style: none; padding: 0; }
.service { font-weight: 500; }
.barber { font-size: .9rem; }
.date { font-size: 1rem; }
.price { font-size: 1.2rem; font-weight: 500; }
</style>