<template>
  <div class="barbershop-contact">
    <v-row>
      <v-col cols="12" class="d-flex justify-start justify-md-end mt-4 mt-md-0">
        <v-tooltip v-model="tooltip" color="success" bottom>
        <template v-slot:activator="{ attrs }">
          <button
            class="btn-link btn-share text-none"
            dark
            text
            v-bind="attrs"
            @click="share"
          >
            <span class="label gray3--text">Compartilhe</span>
            <v-icon class="ml-2">share</v-icon>
          </button>
        </template>
        <span>Link da barbearia copiado!</span>
      </v-tooltip>
      </v-col>
      <v-col>
        <label class="label gray3--text d-block mb-1">
          Contatos e horrios
        </label>
        <a class="btn-link" :href="'tel:' + phoneNumber">
          <v-icon class="mr-2">phone_iphone</v-icon>
          <span class="label gray3--text">
            {{ setPhoneMask(phoneNumber) }}
          </span>
        </a>
      </v-col>
      <v-col cols="auto">
        <label class="label gray3--text d-block mb-1">
          Mdias sociais
        </label>
        <div class="d-flex justify-space-around">
          <a v-if="facebookUrl" :href="facebookUrl" target="_blank" class="btn-link">
            <facebook class="social" />
          </a>
          <a v-if="instagramUrl" :href="instagramUrl" target="_blank" class="btn-link">
            <instagram class="social" />
          </a>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Facebook from '@/components/icons/Facebook'
import Instagram from '@/components/icons/Instagram'

import TextMixin from '@/mixins/TextMixin'
import UserAgent from '@/mixins/UserAgentMixin'

export default {
  components: { Facebook, Instagram },
  mixins: [ TextMixin, UserAgent ],
  props: {
    facebookUrl: { default: null },
    instagramUrl: { default: null },
    phoneNumber: { default: null }
  },
  data: () => ({
    tooltip: false
  }),
  computed: {
    pageUrl () {
      return encodeURI(window.location.href) 
    }
  },
  methods: {
    async copyToClipboard (string) {
      return await Promise.resolve(
        navigator.clipboard.writeText(string)
      ) 
    },
    share () {
      if (this.isMobile()) this.$emit('share')
      else {
        this.copyToClipboard(this.pageUrl).then(() => this.showTooltip())
      }
    },
    showTooltip () {
      this.tooltip = true
      const timoutTime = 3000
      setTimeout(() => {
        this.tooltip = false
      }, timoutTime)
    }
  }
}
</script>

