<template>
  <v-row class="fill-height">
    <v-col>
      <div class="d-flex toolbarapp">
        <v-avatar class="gray2 mr-2 d-flex-start">
          <img v-if="barberImage" :src="barberImage" />
        </v-avatar>
        <div>
          <v-btn
            fab
            text
            small
            color="grey darken-2"
            :disabled="!showBtnPrev"
            @click="prev"
          >
            <v-icon small>
              mdi-chevron-left
            </v-icon>
          </v-btn>
          {{ monthAndYearCmpt }}
          <v-btn fab text small color="grey darken-2" @click="next">
            <v-icon small>
              mdi-chevron-right
            </v-icon>
          </v-btn>
        </div>
        <div></div>
      </div>
      <v-sheet :height="calendarHeight">
        <v-calendar
          ref="calendar"
          v-model="calendar"
          color="primary"
          type="day"
          :first-interval="firstInterval"
          :interval-minutes="intervalMinutes"
          :interval-count="intervalCountCmpt"
          :interval-height="32"
          :events="events"
          :event-color="getEventColor"
          @click:time="addSchedule"
        ></v-calendar>
      </v-sheet>
    </v-col>
  </v-row>
</template>

<script>
import MonthMixin from '@/mixins/MonthMixin';

export default {
  mixins: [MonthMixin],
  props: {
    barberImage: { default: null },
    intervalCount: { default: 96 },
    intervalMinutes: { default: 15 },
    isBarbershopOpen: { default: true },
    startTime: { default: null },
    endTime: { default: null },
    events: { default: () => []}
  },
  data: () => ({
    calendar: null
  }),
  watch: {
    calendar(value) {
      this.$emit('onCalendarChanged', value);
    },
  },
  computed: {
    calendarHeight () {
      return this.isBarbershopOpen ? 300 : 82
    },
    firstInterval() {
      const calendarDate = this.calendar ? new Date(this.calendar.replace('-', '/')) : new Date()
      const now = new Date()
      let time = this.startTime

      if (calendarDate.getDate() == now.getDate()) {
        const nowTime = `${now.getHours()}:${now.getMinutes()}:${now.getSeconds()}`
        if (nowTime > this.startTime) time = nowTime
      }

      if (time) {
        let [hours, minutes] = time.split(':');

        minutes = Number(minutes)
        hours = Number(hours);

        if (hours > 0) {
          minutes += hours * 60;
        }

        if (minutes > this.intervalMinutes) {
          const rest = minutes % this.intervalMinutes
          minutes = minutes - rest
          minutes = minutes / this.intervalMinutes
        }

        return minutes;
      }
      return 0;
    },
    height() {
      return this.isBarbershopOpen ? 600 : null;
    },
    intervalCountCmpt() {
      if (this.endTime) {
        let [hours, minutes] = this.endTime.split(':');

        minutes = Number(minutes);
        hours = Number(hours);

        if (hours > 0) {
          minutes += hours * 60;
        }

        if (minutes > this.intervalMinutes) {
          minutes = minutes / this.intervalMinutes;
        }

        return minutes - this.firstInterval 
      }
      return 0;
    },
    monthAndYearCmpt() {
      const date = this.calendar ? new Date(this.calendar.replace('-', '/')) : new Date();
      const year = date.getFullYear();
      const month = this.getMonthText(date.getMonth());
      return `${month} de ${year}`;
    },
    showBtnPrev() {
      const now = new Date();
      let date = this.calendar ? new Date(this.calendar.replace('-', '/')) : new Date();
      date.setDate(date.getDate());
      return date.getTime() > now.getTime();
    },
  },
  mounted() {
    this.$refs.calendar.checkChange();
  },
  methods: {
    addSchedule(eventDate) {
      const hasEventInThisDateTime = this.hasAnotherEventInDate(eventDate)
      if (!hasEventInThisDateTime) {
        const time = `${eventDate.time.split(':')[0]}:00`
        const dateTime = `${eventDate.date} ${time}`
        this.$emit('addSchedule', dateTime)
      }
    },
    hasAnotherEventInDate (eventDate) {
      const events = [...this.events]
      return events.some(event => {
        const start = event.start.split(' ')[1]
        const end = event.end.split(' ')[1]
        return (eventDate.time >= start && eventDate.time <= end)
      })
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = '';
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
    fetchEvents({ start, end }) {
      const events = [];

      const min = new Date(`${start.date}00:00:00`);
      const max = new Date(`${end.date}23:59:59`);
      const days = (max.getTime() - min.getTime()) / 86400000;
      const eventCount = this.rnd(days, days + 20);

      for (let i = 0; i < eventCount; i++) {
        const allDay = this.rnd(0, 3) === 0;
        const firstTimestamp = this.rnd(min.getTime(), max.getTime());
        const first = new Date(firstTimestamp - (firstTimestamp % 900000));
        const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000;
        const second = new Date(first.getTime() + secondTimestamp);

        events.push({
          name: this.names[this.rnd(0, this.names.length - 1)],
          start: first,
          end: second,
          color: this.colors[this.rnd(0, this.colors.length - 1)],
          timed: !allDay,
          category: this.categories[this.rnd(0, this.categories.length - 1)],
        });
      }

      this.events = events;
    },
  },
};
</script>

<style scoped>
.toolbarapp {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 0 6px 5px;
}
</style>
