<template>
  <div class="choose-time">
    <div v-show="!choosedDateTime">
      <calendar
        :barberImage="barberImage"
        :startTime="startTime"
        :endTime="endTime"
        :isBarbershopOpen="isBarbershopOpen"
        :events="eventsCmpt"
        @addSchedule="addSchedule"
        @onCalendarChanged="setDateCalendar"
        class="mb-4"
      />
      <p v-show="!isBarbershopOpen" class="text-center primaryDark--text">
        A barbearia fechada:
      </p>
    </div>
    <v-row v-show="choosedDateTime" class="primary--text" align="center">
      <v-col class="py-0">{{ choosedDateTimeCmpt }}</v-col>
      <v-spacer></v-spacer>
      <v-col cols="auto" class="py-0">
        <v-btn text @click="removeDate" small>
          <v-icon small>delete</v-icon>
        </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Calendar from '@/components/site/schedule/barber-calendar/Calendar';

import DateMixin from '@/mixins/DateMixin'
import TimeMixin from '@/mixins/TimeMixin'

export default {
  mixins: [ DateMixin, TimeMixin ],
  props: {
    barber: { default: null },
    barbershopId: { required: true },
    choosedDateTime: { default: null },
    events: { default: () => []},
    minimumTime: { default: 0 }
  },
  components: { Calendar },
  data: () => ({
    barbershop: null,
    date: null,
  }),
  computed: {
    barberImage () {
      return this.barber ? this.barber.profile_picture : null
    },
    choosedDateTimeCmpt () {
      if (this.choosedDateTime) {
        const dateTime = new Date(this.choosedDateTime)
        return this.formatDateTimeToStringPtBr(dateTime)
      }

      return null
    },
    dayOfWeek() {
      return this.date.getUTCDay();
    },
    endTime() {
      return this.scheduleDay ? this.scheduleDay.end_time : null;
    },
    eventsCmpt () {
      const events = [...this.events]
      let times = [...this.timesCmpt]
      
      events.forEach(event => {
        const start = event.start.split(' ')[1]
        const end = event.end.split(' ')[1]

        const index = times.findIndex(time => {
          return time.value >= start && time.value <= end
        })

        if (index> -1) times.splice(index, 1)
      })

      times.forEach((time, index) => {
        let totalFreeMinutes = 15
        let i = index + 1

        while (i < times.length) {
          let timeMinutes = this.getTotalMinutesFromTime(time.value)
          const nextTime = times[i]
          const nextTimeMinutes = this.getTotalMinutesFromTime(nextTime.value)
          const minutesToAdd = (i - index) * 15
          timeMinutes += minutesToAdd
         
          if (timeMinutes == nextTimeMinutes) {
            totalFreeMinutes += minutesToAdd
            i++
          } else {
            i = times.length
          }
        }
        
        if (totalFreeMinutes < this.minimumTime) {
          const date = this.dateTime ? new Date(this.dateTime) : new Date()
          const [hour, minute] = time.value.split(':')
          date.setMinutes(minute)
          date.setHours(hour)
          const startDate = this.formatDateTimeToStringEn(date)
          date.setMinutes(date.getMinutes() + 15)
          const endDate = this.formatDateTimeToStringEn(date)
          
          const event = {
            name: 'Indisponvel',
            start: `${startDate}:00`,
            end: `${endDate}:00`,
            color: 'gray2'
          }
          events.push(event)
        }
      })

      return events
    },
    timesCmpt () {
      const times = [... this.times]
      
      if (this.startTime && this.endTime) {
        return times.filter(time => {
          return time.value >= this.startTime && time.value <= this.endTime
        })
      }

      return times
    },
    isBarbershopOpen() {
      return this.scheduleDay ? this.scheduleDay.opened : false;
    },
    scheduleDay() {
      return this.scheduleDays.find((s) => s.day_of_week == this.dayOfWeek);
    },
    startTime() {
      return this.scheduleDay ? this.scheduleDay.start : null;
    },
    scheduleDays() {
      return this.barbershop ? this.barbershop.schedule_days : [];
    },
  },
  mounted() {
    this.date = new Date();
    this.getBarbershopByIdOnService(this.barbershopId);
  },
  methods: {
    addSchedule (dateTime) {
      this.$emit('addSchedule', dateTime)
    },
    removeDate () {
      this.$emit('addSchedule', null)
    },
    setDateCalendar(dateString) {
      this.date = new Date(dateString.replace('-', '/'));
    },
    getBarbershopByIdOnService(barbershopId) {
      this.$axios(`/barbershop/${barbershopId}`).then((response) => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.onGetBarbershopByIdOnService(response.data.corpo);
          }
        }
      });
    },
    onGetBarbershopByIdOnService(barbershop) {
      this.barbershop = barbershop;
    },
  },
};
</script>
