<template>
  <section class="choose-barber">
    <ul class="barbers d-flex pa-0 ma-0">
      <li
        v-for="barber in barbers"
        :key="barber.id"
        class="barber-item pa-2 text-center"
      >
        <div
          class="image gray2 elevation-1 d-inline-block"
          :class="{ active: selectedBarberId === barber.id }"
          @click="selectBarber(barber)"
        >
          <img v-if="barber.profile_picture" :src="barber.profile_picture">
        </div>
        <h1 class="mt-0 mt-1 Primary">
          {{ barber.name }}
        </h1>
      </li>
    </ul>
  </section>
</template>

<script>
export default {
  props: {
    barbers: {
      default: () => []
    },
    selectedBarber: { default: null }
  },
  computed: {
    selectedBarberId () {
      return this.selectedBarber ? this.selectedBarber.id : null
    }
  },
  methods: {
    selectBarber (barber) {
      this.$emit('barberSelected', barber)
    }
  }
}
</script>

<style lang="scss" scoped>
h1 { font-size: .8rem !important; font-weight: 500; }
ul { list-style: none; overflow: hidden; overflow-x: auto; }
.image {
  width: 80px;
  height: 80px;
  overflow: hidden;
  border-radius: 50%; 
  cursor: pointer;
  border: 3px solid transparent;
  &.active {
    border: 3px solid #6cd4ca !important;
  }
}
.image img { width: 100%; }
</style>