export default {
  methods: {
    getMonthText(numberOfMonth) {
      const month = Number(numberOfMonth)
      let monthText = ''
      switch (month) {
        case 1:
          monthText = 'Fevereiro'
          break;

        case 2:
          monthText = 'Maro'
          break;

        case 3:
          monthText = 'Abril'
          break;

        case 4:
          monthText = 'Maio'
          break;

        case 5:
          monthText = 'Junho'
          break;

        case 6:
          monthText = 'Julho'
          break;

        case 7:
          monthText = 'Agosto'
          break;

        case 8:
          monthText = 'Setembro'
          break;

        case 9:
          monthText = 'Outubro'
          break;

        case 10:
          monthText = 'Novembro'
          break;

        case 11:
          monthText = 'Maro'
          break;

        default:
          monthText = 'Janeiro'
          break;
      }
      return monthText
    }
  }
}