<template>
  <choose-template
    title="Escolha os servios:"
    @clicked="setServices"
  >
    <ul class="services">
      <li v-for="service in servicesModel" :key="service.id" class="service pa-2 py-4">
        <v-checkbox
          class="mr-2"
          v-model="service.checked"
        ></v-checkbox> {{ service.name }} <small class="ml-2">R$ {{ service.price | numeric }}</small>
      </li>
    </ul>
 </choose-template>
</template>

<script>
import ChooseTemplate from './ChooseTemplate'

import TextMixin from '@/mixins/TextMixin'

export default {
  components: { ChooseTemplate },
  mixins: [ TextMixin ],
  props: {
    services: {
      default: () => []
    },
    selectedServices: {
      default: () => []
    }
  },
  computed: {
    servicesModel () {
      const services = [...this.services].map(service => {
        return {
          id: service.id,
          name: service.name,
          checked: false,
          price: Number(service.price).toFixed(2),
          duration: service.duration
        }
      })
      this.selectedServices.forEach(selectedService => {
        const index = services.findIndex(service => service.id === selectedService.id)
        services[index].checked = true
      })
      return services 
    }
  },
  methods: {
    setServices () {
      const selectedServices = this.servicesModel.filter(service => service.checked)
      this.$emit('setServices', selectedServices)
    }
  }
}
</script>

<style scoped>
.btn-continue { width: 220px; max-width: 100%; }
.services { 
  list-style: none;
  padding: 0;
  overflow: hidden;
  overflow-y: auto;
  max-height: 200px;
 }
.service { display: flex; align-items: center; border-bottom: 1px solid #ccc; }
small { font-size: .7rem !important; font-weight: 600; align-self: baseline; }
</style> 