<template>
  <section>
    <div class="d-flex align-center">
      <v-btn class="pa-0 text-none" small text @click="addService">
        <v-icon color="success">add</v-icon>
        <span v-show="selectedServices.length > 0">Adicionar outro servio</span>
        <span v-show="selectedServices.length == 0">Adicionar servio</span>
      </v-btn>
    </div>
    <ul v-show="selectedServices.length > 0" class="services mt-2">
      <li v-for="service in selectedServices" :key="service.id" class="service py-0">
        <v-row align="center">
          <v-col class="py-2">
            {{ service.name }}
          </v-col>
          <v-spacer class="d-none d-sm-flex"></v-spacer>
          <v-col cols="auto" class="py-2">
            <v-btn small text @click="removeService(service.id)">
              <v-icon small class="primaryLight--text">
                delete
              </v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </li>
    </ul>
  </section>
</template>

<script>
export default {
  props: {
    selectedServices: {
      default: () => []
    }
  },
  methods: {
    addService () {
      this.$emit('showServiceList')
    },
    removeService (serviceId) {
      this.$emit('removeService', serviceId)
    }
  }
}
</script>

<style scoped>
.services { list-style: none; padding: 0; }
</style>