<template>
  <v-dialog v-model="dialog" :width="500" :retain-focus="false">
    <v-card class="pa-2 pa-sm-4">
      <app-title class="mb-4 title" text="Agende seu horrio" />
      <div v-show="loading" class="loading">
        <v-progress-circular
          indeterminate
          color="primary"
          :width="2"
          :size="60"
        ></v-progress-circular>
        <span class="mt-2">{{ loadingText }}</span>
      </div>
      <div v-show="!loading">
        <section-title text="Servios" />
        <chosen-services
          v-show="showChosenServices && services.length > 0"
          id="chosen-services"
          class="px-2"
          :selectedServices="selectedServices"
          @showServiceList="showChooseServicesCpnt"
          @removeService="removeService"
        />
        <choose-services
          v-show="showChooseServices"
          :services="services"
          :selectedServices="selectedServices"
          @setServices="setSelectedServices"
        />
        <v-divider :height="2" class="my-4"></v-divider>
        <div v-show="showChooseBarbers">
          <section-title text="Escolha o barbeiro que lhe atender" />
          <choose-barber
            :barbers="barbers"
            :selectedBarber="selectedBarber"
            @barberSelected="setSelectedBarber"
          />
          <v-divider :height="2" class="my-4"></v-divider>
        </div>
        <div v-show="showSelectedBarber">
          <section-title text="Escolha um horrio" />
          <choose-time
            :barber="selectedBarber"
            :barbershopId="barbershopId"
            :choosedDateTime="dateTime"
            :events="events"
            :minimumTime="minimumTime"
            @addSchedule="addSchedule"
          />
          <v-divider :height="2" class="mt-4"></v-divider>
        </div>
         <div v-show="showObservation">
          <section-title text="Observao" class="mt-4" />
          <textarea-field
            :label="null"
            placeholder="Ex: Sou alrgico a navalha"
            :outlined="true"
            @onKeyUp="setObservation"
          />
          <v-divider :height="2"></v-divider>
        </div>
        <section-title text="Resumo" class="mt-4" />
        <schedule-summary
          id="schedule-summary"
          class="px-2 mt-2"
          :barberName="choosedBarberName"
          :selectedServices="selectedServices"
          :choosedDateTime="dateTime"
        />
        <div class="mt-6 text-center">
          <v-btn
            class="ma-auto success text-none btn-schedule primary--text"
            :disabled="!enableBtnSchedule"
            :height="50"
            @click="schedule"
          >
            Confirmar Agendamento
          </v-btn>
        </div>
      </div>
      <confirm-alter-schedule-dialog
        ref="confirmAlterScheduleDialog" 
        @confirm="confirmShowChooseServicesCpnt"
      />
    </v-card>
  </v-dialog>
</template>

<script>
import AppTitle from '@/components/site/Title';
import ChosenServices from '@/components/site/schedule/ChosenServices';
import ChooseBarber from '@/components/site/schedule/ChooseBarber';
import ChooseServices from '@/components/site/schedule/ChooseServices';
import ChooseTime from '@/components/site/schedule/ChooseTime';
import ConfirmAlterScheduleDialog from './ConfirmAlterScheduleDialog.vue'
import ScheduleSummary from '@/components/site/schedule/ScheduleSummary';
import SectionTitle from './SectionTitle';
import TextareaField from '@/components/globais/form/textarea-field/TextareaField'

import DateMixin from '@/mixins/DateMixin';
import NavigationMixin from '@/mixins/NavigationMixin';
import SnackbarMixin from '@/mixins/SnackbarMixin';
import TimeMixin from '@/mixins/TimeMixin'

export default {
  components: {
    AppTitle,
    ChosenServices,
    ChooseBarber,
    ChooseServices,
    ChooseTime,
    ConfirmAlterScheduleDialog,
    ScheduleSummary,
    SectionTitle,
    TextareaField
  },
  mixins: [ DateMixin, NavigationMixin, SnackbarMixin, TimeMixin ],
  data: () => ({
    barbers: [],
    barbershopId: null,
    dialog: false,
    events: [],
    services: [],
    dateTime: null,
    loadingText: 'Aguarde',
    lookingForBarbers: true,
    lookingForServices: true,
    savingSchedule: false,
    observation: null,
    selectedBarber: null,
    selectedServices: [],
    showChooseServices: false,
    userId: null
  }),
  computed: {
    choosedBarberName() {
      return this.selectedBarber ? this.selectedBarber.name : null;
    },
    enableBtnSchedule() {
      return (
        this.selectedBarber &&
        this.dateTime &&
        this.selectedServices.length > 0
      );
    },
    loading () {
      return this.lookingForBarbers || this.lookingForServices || this.savingSchedule
    },
    minimumTime () {
      return this.selectedServices.reduce((total, service) => {
        return total += this.getTotalMinutesFromTime(service.duration)
      }, 0)
    },
    showChooseBarbers() {
      return this.selectedServices.length > 0;
    },
    showChosenServices() {
      return !this.showChooseServices;
    },
    showChooseTime() {
      return this.selectedServices.length > 0 && this.selectedBarber;
    },
    showSelectedBarber() {
      return this.selectedBarber;
    },
    showObservation () {
      return this.selectedServices.length > 0 && this.selectedBarber && this.dateTime
    }
  },
  methods: {
    addSchedule(dateTime) {
      this.dateTime = dateTime;
    },
    getBarbers() {
      this.$axios(
        `${this.$url_api}/barbershop/${this.barbershopId}/barber`
      ).then((response) => {
        if (
          Number(response.status) === 200 &&
          Number(response.data.codigo) === 200
        ) {
          this.barbers = response.data.corpo;
        }
      }).finally(() => this.lookingForBarbers = false)
    },
    getSchedule() {
      window.console.log('getScheduling')
      const date = this.dateTime
        ? this.dateTime.split(' ')[0]
        : this.formatDateToDateEn(new Date());
      const barberId = this.selectedBarber.id;
      const url = `${this.$url_api}/barbershop/${
        this.barbershopId
      }/barber/${barberId}/scheduling/date/${date}`;
      this.$axios(url).then((response) => {
        if (
          Number(response.status) === 200 &&
          Number(response.data.codigo) === 200
        ) {
          this.events = response.data.corpo.map(event => {
            return {
              name: 'Agendado',
              start: event.start_date,
              end: event.end_date,
              color: 'primaryLight'
            }
          })
        }
      });
    },
    getServices (selectedServiceId) {
      this.$axios(`${this.$url_api}/barbershop/${this.barbershopId}/service?paginate=false`)
      .then((response) => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.services = response.data.corpo;
            const selectedService = this.services.find(service => service.id === selectedServiceId)
            if (selectedService) {
              this.selectedServices.push({
                id: selectedService.id,
                name: selectedService.name,
                checked: true,
                price: selectedService.price,
                duration: selectedService.duration
              })
            }
          }
        }
      }).finally(() => this.lookingForServices = false)
    },
    hideChooseCpnt() {
      this.showChooseServices = false;
    },
    removeService(serviceId) {
      const index = this.selectedServices.findIndex(
        (selectedService) => selectedService.id === serviceId
      );
      if (index > -1) this.selectedServices.splice(index, 1);
    },
    setSelectedBarber(barber) {
      this.selectedBarber = barber;
      this.dateTime = null
      this.getSchedule();
    },
    setSelectedServices(selectedServices) {
      this.selectedServices = selectedServices;
      this.hideChooseCpnt();
      this.showChosensSections = true;
      this.dateTime = null
    },
    setObservation (observation) {
      this.observation = observation
    },
    schedule() {
      this.loadingText = 'Salvando o agendamento!'
      this.savingSchedule = true
      const startDate = new Date(this.dateTime)
      let endDate = this.selectedServices.reduce((dateTime, service) => {
        let [serviceHours, serviceMinutes] = service.duration.split(':');
        let hours = dateTime.getHours() + Number(serviceHours);
        let minutes = dateTime.getMinutes() + Number(serviceMinutes);
        hours += minutes / 60;
        minutes = minutes % 60;
        dateTime.setHours(hours, minutes);
        return dateTime;
      }, startDate);
      endDate.setMinutes(endDate.getMinutes() - 1)
      endDate = this.formatDateTimeToStringEn(endDate)
      const schedule = {
        barber_id: this.selectedBarber.id,
        client_id: this.userId,
        barbershop_id: this.barbershopId,
        start_date: `${this.dateTime}:00`,
        end_date: `${endDate}:59`,
        observation: this.observation,
        services: this.selectedServices.map((i) => {
          return { id: i.id, price: i.price };
        }),
      }
     this.storeSchedule(schedule)
    },
    storeSchedule(schedule) {
      this.$axios.post(`${this.$url_api}/scheduling/request`, schedule)
        .then(response => {
          if (response.status === 200 && response.data.codigo === 200) this.onStoreScheduleSuccess(response.data.corpo)
          else this.onStoreScheduleError(response.data.corpo)
        }).catch((err) => {
          this.onStoreScheduleError('No foi possvel fazer o agendamento!')
          window.console.log(err)
        }).finally(() => this.savingSchedule = false)
    },
    onStoreScheduleError(error) {
      this.showSnackbarErro(error)
    },
    onStoreScheduleSuccess(response) {
      this.showSnackbar('Agendamento solicitado!');
      const scheduleId = response.id
      this.goTo('site-schedule', { defaultScheduleSelectedId: scheduleId });
      this.hideDialog()
    },
    showChooseServicesCpnt() {
      if (this.dateTime) this.$refs.confirmAlterScheduleDialog.showDialog()
      else this.confirmShowChooseServicesCpnt() 
    },
    confirmShowChooseServicesCpnt () {
      this.showChooseServices = true;
    },
    showDialog(barbershopId, selectedServiceId, userId) {
      this.lookedForBarbers = false
      this.lookedForServices = false
      this.barbershopId = barbershopId
      this.userId = userId
      this.selectedServices = []
      this.getServices(selectedServiceId);
      this.getBarbers();
      setTimeout(() => {
        this.dialog = true
      }, 3000)
    },
    hideDialog () {
      this.dialog = false
    }
  }
}
</script>

<style scoped>
.btn-schedule {
  width: 280px;
  max-width: 100%;
  border-radius: 45px;
}
.loading {
  min-height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.title {
  color: #000000DE !important; 
}
</style>
